import * as React from 'react';
import {Accordion ,AccordionDetails,AccordionSummary,Typography,List,ListItem,ListItemText,Divider,ListItemButton,Grid} from '@mui/material';

import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { FixedSizeList  } from 'react-window';


export default function ControlledAccordions(props) {
const {response,classname,setclassname,setimg}=props
console.log(classname);
 const [confirm,setconfirm]=React.useState('');
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
    console.log(isExpanded,panel,expanded);
  };
  const Changeproduct=(data)=>(event, isExpanded) =>{
    setExpanded(isExpanded ? data['classid']: false);
    setclassname(
    
      (e)=>{
         let newClassname=[]
        console.log(data);
          classname.map((value)=>{
               data['classid']==value.classid? newClassname.push( Object.assign({}, value, data)):newClassname.push(value)
               
                 })
                 return newClassname
        }
  
      
    )
setimg(data['img'])
  

  }

 const renderRow=(props)=>{
  const { index, style,data} = props;
return (<>
  <ListItem key={index} style={style}  >
<ListItem button onClick={Changeproduct(data[index])}  >
  <ListItemText  primary={<Grid container>
<Grid item xs={11}>
{data[index].name}
</Grid>
<Grid item xs={1} >
{'$'+data[index].price}
</Grid >
</Grid>} />
 
  
</ListItem >
<Divider light/>

</ListItem>

</>
 )
 }




const getproduct=(data)=>{

  return response.filter((res)=>res.classid==data);

}

  return (
    <div>
      {classname.map((data)=>(<Accordion expanded={expanded === data['classid']} onClick={props.Check()}key={data['classid']} onChange={handleChange(data['classid'])}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography sx={{ width: '24%', flexShrink: 0 }}>
            {data['classname']}
          </Typography>
          <Typography sx={{ color: 'text.secondary' }}>{data['name']}</Typography>
          <Typography >{data['price']?'$'+data['price']:''}</Typography>

        </AccordionSummary>
        <AccordionDetails>
        <FixedSizeList
        height={300}
        width='100%'
        itemSize={70}
        itemCount={Object.keys( getproduct(data['classid'])).length}
        overscanCount={5}
        itemData={getproduct(data['classid'])}
      >
        {renderRow}

      </FixedSizeList>
        </AccordionDetails>
      </Accordion>
      ))
    
  }  </div>
  );
}
