import React, { useState,useEffect} from 'react';

import {

    Grid,
    Container,
    Card,
    CardMedia,
    CardActionArea
} from '@mui/material'
import Modal from './imginfo'
import Accordion from './producctinfo'
import Request from '../request';
import  Axios  from 'axios';

const SecondExample = (props) => {
    const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
   const {Check}=props
   const [response, setresponse] = useState([]);
   const [classname, setclassname] = useState([]);
   const[img,setimg] = useState('/eval/4/amdr3mpk.jpg');
   useEffect(()=>{
   Axios.all([
    Request().get("./public/index.php?action=getproduct"),
    Request().get("./public/index.php?action=getclass")
   ]) 
    .then(function (resp) {
      console.log(resp);
     let res1 = resp[0]["data"];
     let res2 = resp[1]["data"];

   
      if(window.localStorage){ 
        window.localStorage.setItem("jwtToken", res1['token']);
    }
    
     setresponse(res1['result'])
     setclassname(res2['result'])
  
 
    })
    .catch(function (err) {
      console.log(err);
    });
   },[])
  

 

    return (<>
  
      <Modal open={open} handleClose={handleClose} img={img}></Modal>
        <Container maxWidth="xl" sx={{mt:10}}>
        <Grid container spacing={17}>
        <Grid item xs={5} lg={4}>
        <Card sx={{ maxWidth: '100%', boxShadow:0}}  >
      <CardActionArea onClick={handleOpen}>
        <CardMedia
          component="img"
          image={'https://www.coolpc.com.tw'+img}
          alt="green iguana"
          sx={{mixBlendMode:'multiply'}}
        />
       
      </CardActionArea>
    </Card>
        </Grid>
        {/* ------------------------- row 1 ------------------------- */}
        <Grid item xs={7} lg={7} sx={{paddingLeft:'200px'}}>
         <Accordion  Check={Check} response={response} classname={classname}setclassname={setclassname}setimg={setimg}></Accordion>
        </Grid>
       
      </Grid>
      </Container>
      </>
       
    )
}









export default SecondExample;
 
  
 